import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '../views/Login.vue'
import NotFound from '../views/NotFound.vue'
import User from '../views/User.vue'
//  state
import store from './../store'

Vue.use(VueRouter)

//  404 
const authorizeIsAdmin = (to, from, next) => {
  const adminToken = store.state.adminToken
  //  token  404 
  if (adminToken === null) {
    next('/notfound')
    return
  } 

  next()
}

const authorizeIsUser = async (to, from, next) => {

  //  localStorage  token
  const tokenInLocalStorage = localStorage.getItem('token')

  //  state  token
  const tokenInStore = store.state.token

  //  
  let isAuthenticated = store.state.isAuthenticated

  // localStorage  token  &  store  token 
  if (tokenInLocalStorage && tokenInLocalStorage !== tokenInStore) {
    //  dispatch  Vuex  actions
    isAuthenticated = await store.dispatch('fetchCurrentUser')
  }
  
  //  token 
  // const pathsWithoutAuthentication = ['Regist', 'Login', 'AdminLogin']
  const pathsWithoutAuthentication = ['Regist', 'Login']

  //  token  & 
  if (!isAuthenticated && !pathsWithoutAuthentication.includes(to.name)) {
    next('/Login')
    return
  }

  //  token  & 
  if (isAuthenticated && pathsWithoutAuthentication.includes(to.name)) {
    next('/User')
    return
  }

  next()
}

const router = new VueRouter({
  routes: [
    {
      path: '/',
      name: 'root',
      redirect: '/Login'
    },
    {
      path: '/Login',
      name: 'Login',
      component: Login
    },
    {
      path: '/Regist',
      name: 'Regist',
      component: () => import('../views/Regist.vue')
    },
    {
      path: '/Setting',
      name: 'Setting',
      beforeEnter: authorizeIsUser,
      component: () => import('../views/Setting.vue')
    },
    {
      path: '/Chatroom',
      name: 'Chatroom',
      beforeEnter: authorizeIsUser,
      component: () => import('../views/Chatroom.vue')
    },
    {
      path: '/Message',
      name: 'Message',
      beforeEnter: authorizeIsUser,
      component: () => import('../views/Message.vue')
    },
    {
      path: '/User',
      name: 'User',
      redirect: '/User/Main',
      component: User,
      beforeEnter: authorizeIsUser,
      children: [
        {
          path: 'Main',
          name: 'Main',
          component: () => import('../views/Main.vue'),
        },
        {
          path: 'Notify',
          name: 'Notify',
          component: () => import('../views/Notify.vue'),
        },
        {
          path: 'ReplyList/:id',
          name: 'ReplyList',
          component: () => import('../views/ReplyList.vue'),
        },
        {
          path: 'UserSelf',
          name: 'UserSelf',
          component: () => import('../views/UserSelf.vue'),
        },
        {
          path: 'UserFollow/:id',
          name: 'UserFollow',
          component: () => import('../views/UserFollow.vue'),
        },
        {
          path: 'UserOther/:id',
          name: 'UserOther',
          component: () => import('../views/UserOther.vue'),
        },
      ]
    },
    {
      path: '/AdminLogin',
      name: 'AdminLogin',
      component: () => import('../views/AdminLogin.vue')
    },
    {
      path: '/Admin',
      name: 'Admin',
      redirect: '/Admin/MainList',
      component: () => import('../views/Admin.vue'),
      //  404 
      beforeEnter: authorizeIsAdmin,
      children: [
        {
          path: 'MainList',
          name: 'MainList',
          component: () => import('../views/AdminMainList.vue'),
          beforeEnter: authorizeIsAdmin
        },
        {
          path: 'UserList',
          name: 'UserList',
          component: () => import('../views/AdminUserList.vue'),
          beforeEnter: authorizeIsAdmin
        },
      ]
    },
    {
      path: '*',
      name: 'NotFound',
      component: NotFound
    },
  ]
})

router.beforeEach( (to, from, next) => {
  //  dispatch  Vuex  actions
  // store.dispatch('fetchCurrentUser')

  //   token 
  store.commit("setAdminUser");

  next()
})

export default router
