import Vue from 'vue'
import Vuex from 'vuex'
import currentUserAPI from './../apis/userData'
import userFollowAPI from './../apis/followData'
import { Toast } from "./../utils/helpers"

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    currentUser: {
      id: -1,
      account: "",
      email: "",
      name: "",
      avatar: "",
      cover: null,
      introduction: "",
      role: "",
      totalTweets: 0,
      totalFollowings: 0,
      totalFollowers: 0,
      totalLiked: 0,
      createdAt: "",
      updatedAt: ""
    },
    // 
    isAuthenticated: false,
    token: '',
    //  token
    adminToken: '',
    // id 
    userFollowings: [],
    userFollowers: [],
    popularUsers: [],
  },
  mutations: {
    setCurrentUser(state, currentUser) {
      state.currentUser = {
        ...state.currentUser,
        //  API  currentUser  Vuex state  currentUser
        ...currentUser
      }
      //  true
      state.isAuthenticated = true
      //  token  state 
      state.token = localStorage.getItem('token')
    },
    //   commit  mutations 
    revokeAuthentication(state) {
      state.currentUser = {
        id: -1,
        account: "",
        email: "",
        name: "",
        avatar: "",
        cover: null,
        introduction: "",
        role: "",
        totalTweets: 0,
        totalFollowings: 0,
        totalFollowers: 0,
        totalLiked: 0,
        createdAt: "",
        updatedAt: ""
      }
      state.isAuthenticated = false
      //  state  token 
      state.token = ''
      localStorage.removeItem('token')
    },
    setAdminUser(state) {
      //  token  state 
      state.adminToken = localStorage.getItem('adminToken')
    },
    revokeAdminUser() {
      //  token 
      localStorage.removeItem('adminToken')
    },
    setUserFollowings(state, newUserFollowings) {
      state.userFollowings = newUserFollowings;
    },
    setUserFollowers(state, newUserFollowers) {
      state.userFollowers = newUserFollowers;
    },
    setPopularUsers(state, newPopularUsers) {
      state.popularUsers = newPopularUsers;
    }
  },
  actions: {
    async fetchCurrentUser({ commit }) {
      try {
        //  usersAPI.getCurrentUser()  response 
        const { data } = await currentUserAPI.getCurrentUser()
        commit('setCurrentUser', data)
        // Token ..
        return true
      } catch (error) {
        console.error(error.message)
        //  state  token
        commit('revokeAuthentication')
        // Token ..
        return false
      }
    },
    async fetchUserFollowings({ commit }, { userId }) {
      try {
        //  user  followings 
        const { data } = await userFollowAPI.getUserFollowings(userId);
        //  vuex  user  followings 
        commit('setUserFollowings', data)
        if (data.length === 0) {
          Toast.fire({
            icon: "warning",
            title: "",
          });
        }
      } catch (error) {
        Toast.fire({
          icon: "warning",
          title: error.response.data.message,
        });
      }
    },
    async fetchUserFollowers({ commit }, { userId }) {
      try {
        //  user  followers 
        const { data } = await userFollowAPI.getUserFollowers(userId);
        //  vuex  user  followers 
        commit('setUserFollowers', data)
        if (data.length === 0) {
          Toast.fire({
            icon: "warning",
            title: "",
          });
        }
      } catch (error) {
        Toast.fire({
          icon: "warning",
          title: error.response.data.message,
        });
      }
    },
    async fetchPopularUsers({ commit }) {
      try {
        // 
        const { data } = await userFollowAPI.getPopularList();
        //  vuex 
        commit('setPopularUsers', data)
      } catch (error) {
        Toast.fire({
          icon: "error",
          title: "..",
        });
      }
    }
  },
  modules: {
  }
})
